.services {
  position: relative;
}
.services:before, .services:after {
  content: "";
  background-image: url("/img/sign.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  width: 300px;
  height: 600px;
  left: -120px;
  right: 0;
  top: 0;
  opacity: 0.1;
  z-index: -1;
}
.services:after {
  left: 70%;
  width: 500px;
  height: 900px;
  top: 50%;
}
.services__title {
  text-align: center;
}
.services .container {
  display: grid;
  grid-template-columns: repeat(3, 400px);
  align-items: center;
  justify-content: space-between;
  row-gap: 70px;
}
.services__item {
  display: flex;
  flex-direction: column;
  height: 602px;
}
.services__item:hover svg path {
  fill: var(--main-color);
}
.services__item .item__title {
  text-align: center;
  flex: 0 0 60px;
  margin: 5px 0 10px 0;
}
.services__item .item__link {
  margin: auto auto 0 auto;
}
.services__item .item__list {
  flex: 0 0 200px;
  padding-left: 30px;
  font-size: 18px;
}
.services__item .item__list .list__item {
  margin-bottom: 7px;
  position: relative;
}
.services__item .item__list .list__item:before {
  content: "";
  width: 10px;
  height: 2px;
  position: absolute;
  bottom: 0;
  top: 50%;
  transform: translateY(-50%);
  left: -20px;
  background-color: var(--second-color);
}
.services__item .item__img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 260px;
}
.services__item .item__img .item__icon {
  position: absolute;
  left: 0;
  right: 0;
  width: 60%;
  margin: 0 auto;
}